import { Fragment, useEffect, useState } from "react";
import "./UpdateOrder.css";
import { useAlert } from "react-alert";
import { MdAccountTree } from "react-icons/md";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate, useParams } from "react-router-dom";
import {
  clearErrors,
  getOrderDetails,
  updateOrder,
} from "../../../../redux/actions/orderAction";
import { UPDATE_ORDER_RESET } from "../../../../redux/constants/orderConstant";
import { adminAuth, userAuth } from "../../../../util/userAuth";
import Loader from "../../../layout/Loader/Loader";
import MetaData from "../../../layout/MetaData/MetaData";
import SideBar from "../Dashboard/SideBar/SideBar";
import H2 from "../../../ReusableComponent/Heading/H2/H2";
import Input from "../../../ReusableComponent/Inputs/Input";

const UpdateOrder = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const alert = useAlert();
  const dispatch = useDispatch();

  const [status, setStatus] = useState("");

  const { order, loading } = useSelector((state) => state.orderDetails);
  const { isUpdated, error } = useSelector((state) => state.order);

  const { user, isAuthenticated } = useSelector((state) => state.user);

  const updateStatusHandler = (e) => {
    e.preventDefault();
    const myForm = new FormData();
    myForm.set("status", status);
    dispatch(updateOrder(id, myForm));
  };

  useEffect(() => {
    userAuth(isAuthenticated, navigate);
    adminAuth(true, user, navigate, alert);

    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    if (isUpdated) {
      alert.success("Order Updated Successfully");
      navigate("/admin/orders");
      dispatch({ type: UPDATE_ORDER_RESET });
    }

    dispatch(getOrderDetails(id));
  }, [id, isUpdated, dispatch, error, isAuthenticated, navigate, user, alert]);

  return loading === undefined || loading === true ? (
    <Loader />
  ) : (
    <Fragment>
      <MetaData title={`Update Order`} />

      <div className="dashboard">
        <SideBar />

        <div className="orderPage">
          <div className="confirmShippingArea">
            <div className="orderDetailsCartItemsContainer">
            <H2>Order Items : </H2>
              {order.orderItems &&
                order.orderItems.map((item) => (
                  <div key={item.product}>
                    <Link to={`/product/${item.product}`}>
                      <img src={`/${item.image}`} alt={item.name} />
                      {item.name}
                    </Link>
                    <span>
                      {item.quantity} X {item.price} =
                      <b> {item.quantity * item.price}</b>
                    </span>
                  </div>
                ))}
            </div>
            <div>
            <H2>Shipping Info</H2>
              <div>
                <p>Name : </p>
                <span>{order.user.name}</span>
              </div>
              <div>
                <p>Phone : </p>
                <span>{order.shippingInfo.phoneNo}</span>
              </div>
              <div>
                <p>Address : </p>
                <span>
                  {order.shippingInfo &&
                    `${order.shippingInfo.address} ,
                  ${order.shippingInfo.city} ,
                  ${order.shippingInfo.state} ,
                  ${order.shippingInfo.pinCode} ,
                  ${order.shippingInfo.country}`}
                </span>
              </div>
            </div>
            <div className="orderSummary">
            <H2>Order Summary</H2>
            <div>
              <div>
                <p>Subtotal:</p>
                <span>₹{order.orderItems.reduce((prev,next)=>prev+(next.price*next.quantity),0)}</span>
              </div>
              <div>
                <p>Shipping Charges:</p>
                <span>₹{order.shippingPrice}</span>
              </div>
              <div>
                <p>GST:</p>
                <span>₹{order.taxPrice}</span>
              </div>
              <div>
                <p>
                  <b>Total:</b>
                </p>
                <span>₹{order.totalPrice}</span>
              </div>
            </div>
            </div>
          </div>
          <div className="confirmStatusForm" >
            <div className="orderDetailsContainerBox">
              <div>
          <H2>Payment</H2>
                <p
                  className={
                    order.paymentInfo &&
                    order.paymentInfo.status === "succeeded"
                      ? "greenColor"
                      : "redColor"
                  }
                >
                  {order.paymentInfo && order.paymentInfo.status === "succeeded"
                    ? "Paid"
                    : "Not Paid"}
                </p>
              </div>
            </div>
            <div className="orderDetailsContainerBox">
              <div>
            <H2>Order Status</H2>
                <p
                  className={
                    order.orderStatus && order.orderStatus === "Delivered"
                      ? "greenColor"
                      : "redColor"
                  }
                >
                  {order.orderStatus && order.orderStatus}
                </p>
              </div>
            </div>
            <form className="statusForm" onSubmit={updateStatusHandler} style={order.orderStatus === "Delivered" ?{display:"none"}:{}}>
            <H2>Edit Order Status</H2>
             
                
                <Input
                type="select"
                icon={<MdAccountTree />}
                  name="status"
                  onChange={(e) => setStatus(e.target.value)}
                >
                  <option value="">Select Category</option>
                  {order.orderStatus === "Processing" && (
                    <option value="Shipped">Shipped</option>
                  )}
                  {order.orderStatus === "Shipped" && (
                    <option value="Delivered">Delivered</option>
                  )}
                </Input>
          

                <Input
                type="submit"
                  value=" Update"
                  disabled={loading ? true : false}
                />
                 
            </form>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default UpdateOrder;
